<template>
  <div class="home">
    <div class="opponent">
      <button class="btn"
        v-for="(item, key) in opponent"
        :key="key"
        :disabled="active.indexOf(item.value) === -1"
        @click="handleOpponent(item)">
        {{ item.text }}
      </button>
      <router-link class="btn" to="/log"></router-link>
    </div>

    <Modal v-model="showSiteModal" title="" okText='' @ok="goGage">
      <cForm ref="siteForm" @submit="goGage" :formData="siteFormData" :rules="siteRules">
        <FormGroup v-model="siteFormData.userName" label="" placeholder="" />
        <FormGroup v-model="siteFormData.opponentName" label="" placeholder="" />
        <FormGroup v-model="siteFormData.countDown" label="0" placeholder="" type="number" :min="0" />
      </cForm>
    </Modal>

    <Modal v-model="showComputerModal" title="" okText='' @ok="goComputer">
      <cForm ref="computerForm" @submit="goComputer" :formData="computerFormData" :rules="computerRules">
        <FormGroup v-model="computerFormData.userName" label="" placeholder="" />
        <FormGroup v-model="computerFormData.selectedColor" label="" type="radio" :values="[pieceColor.black, pieceColor.white]"/>
        <FormGroup v-model="computerFormData.countDown" label="0" placeholder="" type="number" :min="0" />
      </cForm>
    </Modal>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import { opponent, pieceColor, USER_NAME, OPPONENT_NAME } from '../constant'
import { cloneDeep, pick } from '../tools'
import Modal from '../components/Modal'
import FormGroup from '../components/FormGroup'
import cForm from '../components/Form'

export default {
  name: 'home',
  components: {
    Modal,
    FormGroup,
    cForm
  },
  data () {
    return {
      // 
      opponent: cloneDeep(opponent),
      pieceColor: cloneDeep(pieceColor),
      // 
      active: [opponent.site.value, opponent.simpleComputer.value],
      //  created 
      rules: {
        userName: [
          { required: true, message: '' }
        ],
        opponentName: [
          { required: true, message: '' }
        ],
        selectedColor: [
          { rgx: `^(${pieceColor.black.value}|${pieceColor.white.value})$`, message: '' }
        ],
        countDown: [
          { rgx: `^([0-9]+)$`, message: '' }
        ]
      },

      // 
      showSiteModal: false,
      siteFormData: {
        userName: localStorage.getItem(USER_NAME) || '',
        opponentName: localStorage.getItem(OPPONENT_NAME) || '',
        countDown: 0
      },
      siteRules: [],

      // 
      showComputerModal: false,
      computerFormData: {
        userName: localStorage.getItem(USER_NAME) || '',
        selectedColor: pieceColor.black.value,
        countDown: 0
      },
      computerRules: [],
    }
  },
  created () {
    window.home = this

    // state
    this.reset()

    this.siteRules = {
      ...pick(this.rules, ['userName', 'countDown']),
      opponentName: this.rules.opponentName
    }
    this.computerRules = pick(this.rules, ['userName', 'selectedColor', 'countDown'])
  },
  methods: {
    ...mapMutations(['setUser', 'setOpponent', 'setMode', 'setCountDown', 'reset']),
    // 
    handleOpponent (data) {
      this.setMode(data)

      if (data.value === opponent.site.value) {
        this.showSiteModal = true
      } else {
        this.showComputerModal = true
      }
    },
    // 
    goGage () {
      // 
      if (!this.$refs['siteForm'].validate()) {
        return
      }

      // 
      localStorage.setItem(USER_NAME, this.siteFormData.userName)
      localStorage.setItem(OPPONENT_NAME, this.siteFormData.opponentName)

      // 
      this.setCountDown(this.siteFormData.countDown)
      // 
      this.setUser({
        name: this.siteFormData.userName,
        color: pieceColor.black,
        fraction: 0
      })
      this.setOpponent({
        name: this.siteFormData.opponentName,
        color: pieceColor.white,
        fraction: 0
      })

      this.$router.push('game')
    },
    // 
    goComputer () {
      // 
      if (!this.$refs['computerForm'].validate()) {
        return
      }

      // 
      localStorage.setItem(USER_NAME, this.computerFormData.userName)

      // 
      this.setCountDown(this.computerFormData.countDown)
      // 
      this.setUser({
        name: this.computerFormData.userName,
        color: this.computerFormData.selectedColor === pieceColor.black.value ? pieceColor.black : pieceColor.white,
        fraction: 0
      })
      this.setOpponent({
        name: opponent.simpleComputer.text,
        color: this.computerFormData.selectedColor === pieceColor.black.value ? pieceColor.white : pieceColor.black,
        fraction: 0
      })

      this.$router.push('game')
    }
  }
}
</script>

<style lang="stylus" scoped>
.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 40px 0;
}

.opponent {
  display: flex;
  flex-direction: column;
  align-items: center;

  .btn {
    margin-top: 20px;
  }
}
</style>

